___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --header: #c75b7a;
  --header-highlight: #00000010;
  --background: #921a40;
  --font-color: #f5f5f5;
  --secondary-font-color: #f4d9d0;
  --alternative-font-color: #000000;
  --placeholder: #00000090;
  --accent: #d9abab;
  --checkmark: #b03052;
  --error: #6d0d0d;

  --duet-color-primary: #921a40;
  --duet-color-text: #333;
  --duet-color-text-active: #f4d9d0;
  --duet-color-placeholder: #00000090;
  --duet-color-button: #b03052;
  --duet-color-surface: #d9abab;
  --duet-color-overlay: rgba(0, 0, 0, 0.8);
  --duet-color-border: #333;

  --duet-font: Yantramanav, -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Helvetica, Arial, sans-serif;
  --duet-font-normal: 400;
  --duet-font-bold: 600;

  --duet-radius: 4px;
  --duet-z-index: 600;
}

body {
  font-family: Yantramanav, sans-serif;
  color: var(--font-color);
  background-color: var(--background);
  user-select: none;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

button,
select,
input[type='checkbox'] {
  cursor: pointer;
}

dialog {
  margin: auto;
}

:focus {
  outline: none;
}

header {
  background-color: var(--header);
  padding: 5px 0;
}

.task-filter {
  list-style-type: none;
  height: 4rem;
  padding: 0 30%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5px;
  font-size: 1.25rem;
  cursor: pointer;
}

.task-filter > li {
  height: inherit;
  width: 100%;
  display: grid;
  align-items: center;
  justify-content: center;
}

#daily {
  background-color: var(--header-highlight);
}

.date-section {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  padding: 5rem 40%;
  gap: 50px;
}

.date-container {
  width: 100%;
}

.day {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
}

.date {
  font-weight: 300;
  text-align: center;
  color: var(--secondary-font-color);
  white-space: nowrap;
}

.previous-day,
.next-day {
  font-size: 2.5rem;
  font-weight: bold;
  color: var(--accent);
  background-color: transparent;
  border: none;
}

.previous-day:active,
.next-day:active {
  transform: scale(1.1);
  transition: 0.25s ease;
}

main {
  padding: 0.5rem 30%;
}

.input-wrapper {
  position: relative;
}

.input-wrapper > input {
  font-size: 1rem;
  background-color: var(--accent);
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 2.5rem;
  padding: 0 1rem;
}

.input-wrapper > button {
  visibility: hidden;
  background: transparent;
  border: none;
  color: var(--placeholder);
  font-size: 1.1rem;
  position: absolute;
  height: 100%;
  padding-right: 1rem;
  right: 0;
}

::placeholder {
  color: var(--placeholder);
  font-family: Yantramanav, sans-serif;
}

.pinned-container {
  margin-top: 1rem;
  border-bottom: solid 1px var(--accent);
}

.pinned-container > p {
  font-size: 0.75rem;
  font-weight: bold;
  color: var(--secondary-font-color);
  margin: 0.5rem 0;
}

.pinned-tasks {
  margin-bottom: 1rem;
}

.tasks-list {
  margin-top: 1rem;
}

.task-container {
  display: flex;
  gap: 1rem;
  padding: 0.25rem 0;
  align-items: center;
  margin-bottom: 0.5rem;
}

.task-details {
  overflow: hidden;
}

.completed {
  text-decoration: line-through;
  opacity: 50%;
}

.task-note {
  font-size: 0.9rem;
  font-weight: 200;
}

.task-time {
  font-size: 0.75rem;
  margin-left: auto;
}

.manage-task {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--font-color);
  padding: 0.5rem;
  background-color: transparent;
  border: none;
}

input[type='checkbox'] {
  appearance: none;
  -webkit-appearance: none;
  display: flex;
  align-content: center;
  justify-content: center;
  font-size: 2rem;
  padding: 0.1rem;
  border: none;
  border-radius: 50%;
  background-color: var(--accent);
}

input[type='checkbox']::before {
  content: '';
  width: 1rem;
  height: 1rem;
  clip-path: polygon(28% 38%, 41% 53%, 75% 24%, 86% 38%, 40% 78%, 15% 50%);
  transform: scale(0);
  background-color: var(--checkmark);
}

input[type='checkbox']:checked::before {
  transform: scale(1);
}

.task-container > input[type='checkbox']:active {
  transform: scale(1.15);
  transition: 0.1s ease;
}

main > dialog[open] {
  color: var(--secondary-font-color);
  background-color: var(--header);
  padding: 2rem;
  border: none;
  border-radius: 10px;
  width: 40vw;
  position: relative;
}

.task-form {
  display: flex;
  flex-direction: column;
}

.task-form input {
  background-color: var(--accent);
  font-size: 1rem;
  border: none;
  padding: 0.5rem;
  border-radius: 5px;
  margin-bottom: 1rem;
}

.category-wrapper {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 0.75rem 0;
}

#category {
  border: none;
  width: 100%;
  font-size: 1rem;
  color: var(--alternative-font-color);
  background-color: var(--accent);
  padding: 0.5rem;
  border-radius: 5px;
}

.task-form input:focus,
#category:focus {
  outline: 2px dotted var(--background);
}

input[type='time'] {
  font-family: Yantramanav;
}

fieldset {
  border: none;
  margin-top: 0.5rem;
}

legend {
  margin-bottom: 0.5rem;
}

.daily-recurrence,
.weekly-recurrence {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  justify-items: center;
}

.weekly-recurrence {
  display: none;
  margin: 0.5rem 0;
}

.daily-recurrence input:focus,
.weekly-recurrence input:focus {
  outline: none;
}

.daily-recurrence input,
.weekly-recurrence input {
  margin-bottom: 0;
  cursor: pointer;
}

.weekly-recurrence input {
  appearance: none;
  -webkit-appearance: none;
  transform: scale(2);
  border-radius: 2.5px;
}

.daily-recurrence input:checked,
.weekly-recurrence input:checked {
  background-color: var(--checkmark);
}

.daily-recurrence input:checked::before {
  all: unset;
  content: '';
  width: 1rem;
  height: 1rem;
}

.daily-recurrence div,
.weekly-recurrence div {
  display: grid;
  position: relative;
}

.daily-recurrence label,
.weekly-recurrence label {
  font-weight: bold;
  color: var(--secondary-font-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  pointer-events: none;
}

.error-daily {
  margin-top: 1rem;
  text-align: center;
  color: var(--error);
}

input[type='submit'] {
  margin-top: 2rem;
  border: none;
  padding: 0.75rem;
  border-radius: 5px;
  font-weight: bold;
  color: var(--secondary-font-color);
  background-color: var(--background);
  cursor: pointer;
}

.cancel-btn {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--accent);
  background-color: var(--background);
  position: absolute;
  top: 0;
  right: 0;
  margin: 0.25rem;
  border: none;
  border-radius: 25%;
  height: 2rem;
  width: 2rem;
}

.manage-modal[open] {
  width: 4.5rem;
  margin: 0;
  margin-top: -2.6rem;
  margin-left: 1rem;
  border-radius: 5px;
  padding: 0;
  overflow: visible;
}

.manage-list {
  padding: 0.5rem 1rem;
}

.manage-task {
  list-style-type: none;
  line-height: 1.1rem;
  font-size: 1rem;
  font-weight: inherit;
  color: var(--secondary-font-color);
  padding: 0.1rem 0;
  cursor: pointer;
}

.manage-task:hover {
  color: var(--font-color);
}

.manage-arrow {
  height: 1rem;
  width: 1rem;
  background-color: var(--header);
  position: absolute;
  top: 45%;
  left: -0.75rem;
  clip-path: polygon(100% 0, 0 50%, 100% 100%);
}

.confirm-delete[open] {
  width: fit-content;
  padding: 1rem;
  border-radius: 5px;
  display: flex;
  gap: 1rem;
  align-items: center;
}

.confirm-delete[open] > button {
  border: none;
  border-radius: 3px;
  width: 3rem;
  background-color: var(--checkmark);
  color: var(--secondary-font-color);
  padding: 0.5rem;
}

.confirm-delete[open] > button:hover {
  opacity: 80%;
}

::-webkit-scrollbar {
  width: 7.5px;
  height: 7.5px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: var(--checkmark);
  border-radius: 4px;
}

::-webkit-scrollbar-corner {
  background-color: var(--header);
  border-bottom-right-radius: 4px;
}

/* Duet Library */
.duet-date__toggle {
  height: inherit;
  width: 2.5rem;
  top: 0;
  right: 0;
  bottom: 1rem;
  margin: 0;
}

.duet-date__input {
  pointer-events: none;
}

.duet-date__toggle-icon svg {
  color: var(--duet-color-surface);
}

.duet-date__prev,
.duet-date__next {
  color: var(--duet-color-surface);
}

.duet-date__select--year > option,
.duet-date__select--month > option {
  background-color: var(--duet-color-surface);
}

.duet-date__select--year::-webkit-scrollbar {
  background-color: var(--duet-color-surface);
}

.is-today {
  border: 2px solid var(--duet-color-primary);
}

@media only screen and (max-width: 1024px) {
  .task-filter {
    padding: 0 20%;
  }

  .date-section {
    padding: 5rem 35%;
  }

  main {
    padding: 0.5rem 20%;
  }

  main > dialog[open] {
    width: 60vw;
  }
}

@media only screen and (max-width: 600px) {
  .task-filter {
    padding: 0 12.5%;
    gap: 25px;
  }

  .date-section {
    padding: 5rem 1%;
  }

  .date-container {
    width: 200px;
  }

  main {
    padding: 0.5rem 12.5%;
  }

  main > dialog[open] {
    width: 80vw;
  }

  .manage-modal[open] {
    width: 4.5rem;
    margin-left: -6rem;
  }

  .manage-arrow {
    left: 4.25rem;
    clip-path: polygon(100% 50%, 0 0, 0 100%);
  }
}

/* Sortable JS */
.drag-handle button,
.drag-handle input[type='checkbox'] {
  pointer-events: none;
}
